@import url(https://fonts.googleapis.com/css?family=Acme&display=swap);
@import url(https://fonts.googleapis.com/css?family=Acme|Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Acme|Forum|Fredoka+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}




.cell{position:relative}.empty{background:url(/static/media/pustpole.ff9ee3bc.jpg);background-size:cover}.chicken_inside{background:url(/static/media/kur.ff8e83f9.jpg);background-size:cover}.chicken_inside .shown{transform:translateX(calc(50% - 14px))}.rye_inside{background:url(/static/media/pshenichnoepole.94db500a.jpg);background-size:cover}.rye_inside .shown{transform:translateX(calc(50% - 45px))}.cow_inside{background:url(/static/media/cow.8d5914d4.jpg);background-size:cover}.cow_inside .shown{transform:translateX(calc(50% - 21px))}.btn{position:absolute;bottom:0}.hidden{opacity:0}.sell__cell{transform:translateX(calc(50% - 25px))}.shown{transition:all 0.5s;opacity:1}

.field{max-width:710px;margin:10px auto;margin-top:250px;display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:10px;grid-auto-rows:200px;grid-auto-flow:dense}.buyLand{top:180px;height:50px}

.infolist{position:absolute;display:flex;top:20px;left:200px;z-index:9}.value{padding-left:30px;font-style:italic;font-size:24px}.val{font-family:'Fredoka One', cursive}.red{color:#9b1b1b}.green{color:#30ac30}

.btnDeclr{top:160px;width:200px;height:50px}

.household{width:100px;height:100px;position:absolute}.household .btn{opacity:0}.none{display:none}.ball{opacity:0}.dragged{position:static;display:block}.dragged .btn{position:absolute;bottom:95px;opacity:1}.dragged .feed{left:12px}.dragged .harvest{right:12px}.dragged .food__indicator{position:absolute;width:12px;height:100%;background:#fd4747;left:0;top:0}.dragged .generate__indicator{position:absolute;background:#fd4747;width:12px;height:100%;right:0;top:0;transform:rotate(180deg)}.dragged .generate__indicator .scale{border-radius:0 0 3px 3px;background:yellow;width:100%;transition:all 0.05s;transition:background-color 1.2s ease}.dragged .ball{background:white;border-radius:50%;position:absolute;bottom:10px;left:3px;width:30px;height:30px;display:flex;justify-content:center;align-items:center;opacity:1}

.dragged_chicken{color:#0b096d;font-size:22px;text-align:center;font-weight:900}.wait_for_drag-chicken{top:230px;left:32px;background-image:url(/static/media/chicken.15cb847a.jpg);background-size:cover}

.wait_for_drag-cow{background:#10cabb;background-image:url(data:image/png;base64,);background-size:cover;top:330px;left:32px}.dragged_cow{color:#6d69e2;font-size:22px;text-align:center;font-weight:900}

.wait_for_drag-rye{top:78px;left:32px;background:hotpink;background-image:url(data:image/jpeg;base64,);background-size:cover}.wait_for_drag-rye .btn{position:absolute;bottom:55px;left:0}.dragged_rye{color:#6d69e2;font-size:22px;text-align:center;font-weight:900}

.sellBtn_wrapper{display:flex;flex-direction:column;padding-left:32px;position:absolute;top:490px}.sellBtn{position:static;width:160px;margin-bottom:14px}.btnEggs{border:none !important;background-color:orange !important}.btnMilk{border:none !important;background-color:#177523 !important}.btnNoth{font-weight:900 !important;color:goldenrod !important;font-size:20px !important;background:linear-gradient(to top left, powderblue, pink) !important;border:3px solid goldenrod !important}

.curs{position:absolute;top:90px;right:30px;padding-top:160px;padding-left:40px;background:url(data:image/jpeg;base64,);background-size:cover;width:490px;height:430px;box-sizing:border-box;font-size:22px;font-family:'Pacifico';color:#302e2e;font-weight:bolder}@media screen and (max-width: 1400px){.curs{font-size:14px;background:none;width:320px}}

.popup{position:fixed;width:100%;height:100vh;top:0;background:rgba(208,232,255,0.95);z-index:9}.instruction{width:600px;text-align:center;margin:20px auto;overflow:auto}.instruction .btn{position:static;margin-top:23px;margin-left:12px}.instruction .nextBtn_4,.instruction .prevBtn_1{display:none}.clear{top:20px;right:30px;height:40px;width:130px}.hide{background:none;margin-top:calc(100vh - 100px);z-index:0;width:210px;height:110px}.hide .instruction{font-size:0}.hide .instruction .btn{display:none}

